.quiz-container {
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-image: linear-gradient(red, tomato);
}

.link-item {
    display: flex;
    gap: 100px;
    color: white;

}

.link {
    color: white;
    text-decoration: none;
    font-weight: 600;
    padding: 10px;
    border-radius: 20px;
}

.link:hover {
    background-color: black;
}